<template>
  <el-card class="box-card">
    <el-row :gutter="20">
      <el-col :span="16" :offset="2">
        <el-input v-model="newTitle" size placeholder="请输入待办事项..." />
      </el-col>
      <el-col :span="6">
        <el-button type="primary" icon="el-icon-plus" @click="handleAdd" circle></el-button>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-table :data="tableData" style="width: 100%" :row-class-name="tableRowClassName">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column align="center" label="待办事项" prop="content"></el-table-column>
      <el-table-column align="right" label="操作">
        <template slot-scope="scope">
          <!-- 更改状态 -->
          <el-button
            type="success"
            icon="el-icon-check"
            v-show="scope.row.status == 0"
            @click="handleEdit(scope.$index, scope.row)"
            circle
          ></el-button>
          <el-button
            type="warning"
            icon="el-icon-refresh-left"
            v-show="scope.row.status == 1"
            @click="handleEdit(scope.$index, scope.row)"
            circle
          ></el-button>
          <!-- 删除 -->
          <el-button
            type="danger"
            icon="el-icon-close"
            @click="handleDelete(scope.$index, scope.row.id)"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
</template>

<script>
export default {
  name: "TodoList",
  data() {
    return {
      tableData: [],
      newTitle: ""
    };
  },
  mounted() {
    this.getTodoList();
  },
  methods: {
    tableRowClassName({ row }) {
      if (row.status) {
        return "success-row";
      } else {
        return "";
      }
    },
    // 查询所有
    getTodoList() {
      this.axios
        .get("/api")
        .then(response => (this.tableData = response.data));
    },
    // 更新
    handleEdit(index, row) {
      const id = row.id
      let status = row.status == 0 ? 1 : 0;
      let messageSuffix = status == 0 ? "置为未完成": "置为已完成"
      this.axios
        .put(`/api/${id}`,{
          params:{
            state:status
          }
        })
        .then(() => {
          this.tableData[index].status = status;
          this.$message({
            showClose: true,
            duration: 1500,
            message: `<${row.content}> ${messageSuffix}`,
            type: "success"
          });
        });
    },
    // 删除
    handleDelete(index, id) {
      this.axios.delete(`/api/${id}`).then(() => {
        this.tableData.splice(index, 1);
        this.$message({
          showClose: true,
          duration: 1500,
          message: "删除待办事项成功",
          type: "success"
        });
      });
    },
    // 增加
    handleAdd() {
      if (this.newTitle != "") {
        this.axios
          .post(`/api`, {
            content: this.newTitle
          })
          .then(() => {
            this.getTodoList();
            this.$message({
              showClose: true,
              duration: 1500,
              message: "添加待办事项成功",
              type: "success"
            });
          });
        this.newTitle = "";
      } else {
        this.$message({
          showClose: true,
          duration: 1500,
          message: "title不能为空哦",
          type: "warning"
        });
      }
    }
  }
};
</script>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  text-decoration: line-through;
}
</style>